import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Accordion, AccordionDetails, AccordionSummary, Box, Grid, Typography } from "@mui/material";
import { Map, YMaps } from '@pbe/react-yandex-maps';
import React from "react";
import theme from '../theme';
import ContentWrapper from "./ContentWrapper";
import CustomListItem from './CustomListItem';
import CustomTitle from "./CustomTitle";

const ItemWrapper = (props) => (
    <Box sx={{ mb: 2 }}>
        <Typography variant="h5" sx={{ mb: 1 }}>{props.title}</Typography>
        <Typography variant="text">{props.content}</Typography>
        {props.children}
    </Box>
)

const Contacts = () => {
    return (
        <ContentWrapper bgColor="#CC3A5A" id="contacts" color="white">
            <CustomTitle title="контакты" />

            <Grid container spacing={{ xs: 1, sm: 6 }} sx={{ textAlign: "left" }}>
                <Grid item xs={12} sm={6}>
                    <ItemWrapper title="бронирование" content={"+7 (495) 212-91-71"} />
                    <ItemWrapper title="адрес" content={"м. Парк Победы, ул. Братьев Фонченко, дом 10, строение 1"} />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <ItemWrapper title="график работы" content={"каждый день с 12:00 до 23:00"} />
                </Grid>
            </Grid>

            <Box id="important" sx={{ textAlign: "left" }}>
                <ItemWrapper title="важно!">
                    <CustomListItem src={"/images/icons/exclam.png"}>оплата только наличными или СБП</CustomListItem>
                    <Accordion defaultExpanded >
                        <AccordionSummary sx={{ margin: 0 }}
                            expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                            aria-controls="panel1-content"
                            id="panel1-header"
                        >
                            <img src={"/images/icons/exclam.png"} alt="ops" style={{ width: "1.5em", marginRight: "2%" }} />
                            <Typography variant="text">наши правила</Typography>
                        </AccordionSummary>
                        <AccordionDetails >
                            <Typography sx={{ textAlign: "left", textDecorationColor: theme.palette.background.default, }}>
                                Если вы находитесь в отдельной комнате, вы можете пользоваться и оборудованием этой комнаты и инфраструктурой общих залов (пинг-понгом, кикером, настольным хоккеем и футболом, игротекой и приставкой. Единственное, нельзя занимать диванные зоны в общих залах.<br /><br />
                                Если вы решите сменить комнату, перейти в другую или занять диванную зону в общем зале, то ваши карты учета времени нужно закрыть, произвести по ним расчёт и открыть новые карты, после чего начнётся новый отсчёт вашего пребывания в антикафе. При смене места в общем зале на отдельную комнату действует тоже правило. Другими словами, при смене локации, безлимит сгорает.<br /><br />
                                Для <u>безналичной оплаты</u>, пожалуйста, заранее подключите в Вашем банковском приложении СБП — Систему быстрых платежей по QR коду.<br /><br />
                                Действует возрастной ценз 14+.<br /><br />
                                Обязательно забронируйте место заранее!
                            </Typography>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                            aria-controls="panel2-content"
                            id="panel2-header"
                        >
                            <img src={"/images/icons/exclam.png"} alt="ops" style={{ width: "1.5em", marginRight: "2%" }} />
                            <Typography variant="text">путь пешком от метро</Typography>
                        </AccordionSummary>
                        <AccordionDetails >
                            <Typography sx={{ textAlign: "left" }}>
                                Дорога от метро до нас проходит прямо через Поклонную гору. Вам нужно дойти до обелиска, повернуть налево, пройти через арку дома №10 и повернуть еще раз налево, перед вами покажется наш особняк.<br /><br />
                                Обязательно забронируйте место заранее.<br /><br />
                                Мы находимся в парке, поэтому к антикафе нельзя подъехать на автомобиле. Машину лучше оставить на бесплатной парковке со стороны ул. Минская. За АЗС поворот направо, объезжаете АЗС и мечеть, проезжаете около 1 км и перед шлагбаумом можно запарковаться.<br /><br />
                                От КПП до антикафе 350 метров пешком.
                            </Typography>
                        </AccordionDetails>
                    </Accordion>
                </ItemWrapper>
            </Box>

            <YMaps>
                <Map defaultState={{ center: [55.729493, 37.510942], zoom: 15 }} width={"100%"} height={"300px"} />
            </YMaps>
            <Typography sx={{ fontWeight: "bold", paddingTop: "16px" }}>Если Вам нужен наш срочный ответ, позвоните, пожалуйста, по телефону: +7 (495) 212-91-71. <br />На сообщения не успеваем отвечать быстро.</Typography>

        </ContentWrapper >
    )
}

export default Contacts;