import { Grid, Link, Typography } from "@mui/material";
import React from "react";
import theme from "../theme";
import ContentWrapper from "./ContentWrapper";
import CustomListItem from "./CustomListItem";
import CustomTitle from "./CustomTitle";

const Price = () => {
    return (
        <ContentWrapper bgColor="#F1275B" id="price" color="white">
            <CustomTitle title="наш прайс">
                <Typography variant="text">в антикафе действует клубная система</Typography>
            </CustomTitle>
            <Grid container sx={{ textAlign: "left", mb: 3 }} spacing={6}>
                <Grid item xs={12} sm={6} >
                    <Typography variant="h5" sx={{ mb: 1 }}>стандарт</Typography>
                    <CustomListItem src={"/images/icons/1.png"}>
                        <b>обычные условия</b> — 3 руб. в минуту.
                    </CustomListItem>
                    <CustomListItem src={"/images/icons/2.png"}>
                        <b>«минимальный чек»</b> — 180 р. (первые 60 минут округляются до часа).
                    </CustomListItem>
                    <CustomListItem src={"/images/icons/3.png"}>
                        <b>«максимальный чек»</b> (стопчек) — 500 р. (безлимит на весь день).
                    </CustomListItem>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography variant="h5" sx={{ mb: 1 }}>караоке</Typography>
                    <CustomListItem src={"/images/icons/1.png"}>
                        <b>обычные условия</b> — 180 р. час, стоп чек 500 р.
                    </CustomListItem>
                    <CustomListItem src={"/images/icons/exclam.png"}>
                        акции в караоке <b>не действуют</b>
                    </CustomListItem>
                    <CustomListItem src={"/images/icons/exclam.png"}>
                        время каждого гостя учитывается <b>отдельно</b>
                    </CustomListItem>
                    <Link href="#important" sx={{ color: "inherit", textDecorationColor: theme.palette.background.default, ":hover": { textDecorationColor: theme.palette.background.default, opacity: 0.6 } }}>
                        <CustomListItem src={"/images/icons/exclam.png"}>детали бронирования</CustomListItem>
                    </Link>
                </Grid>
            </Grid>
            <Grid container sx={{ textAlign: "left" }} spacing={6}>
                <Grid item xs={12} sm={6}>
                    <Typography variant="h5">бронирование отдельных комнат</Typography>
                    <Typography variant="text">
                        Отдельные комнаты бронируются на общих основаниях, никаких дополнительных платежей нет.<br /><br />Бронь отдельных комнат возможна только для компаний от восьми человек. Это условие распространяется и на комнату караоке.<br /><br />В комнате караоке студенческая акция не действует.<br /><br />
                        Бронируя комнату, обязательно расскажите о всех особенностях вашего мероприятия и о ваших потребностях, предупредите о дополнительном оборудовании, которое вам понадобится, чтобы мы все заранее подготовили.
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography variant="h5">акции для студентов</Typography>
                    <Typography variant="text">
                        В будни с понедельника по пятницу для студентов действует безлимит 300 р.<br />Чтобы воспользоваться акцией, нужно подписаться на антикафе в ВК и предъявить студенческий билет или социальную карту.<br /><br />
                        Студенческая акция не действует в выходные дни, даже если они выпадают на пн., вт., ср., чт., пт.
                    </Typography>
                </Grid>
            </Grid>
        </ContentWrapper >
    )
}

export default Price;