import { Grid, Typography } from "@mui/material";
import React from "react";
import theme from "../theme";
import ContentWrapper from "./ContentWrapper";
import PhotosShower from "./PhotosShower";
import PhotoShower from "./PhotoShower_1";

const photosInterier = [
    { srcP: "place2.jpg" },
    { srcP: "place1.jpg" },
    { srcP: "fireplace.jpg" },
    { srcP: "fireplace1.jpg" },
    { srcP: "hall.jpg" },
    { srcP: "mainhall.jpg" },
    { srcP: "mainhall1.jpg" },
    { srcP: "sofa.jpg" },
];
const photosKaraoke = [
    { srcP: "karaoke.jpg" },
    { srcP: "karaoke1.jpg" },
    { srcP: "karaoke2.jpg" },
];

const Interier = () => {

    return (
        <ContentWrapper bgColor="white" id="interier" color="black">
            <Grid container sx={{ textAlign: "left" }} spacing={{ xs: 2, sm: 6 }}>
                <Grid xs={12} sm={6} item>
                    <PhotoShower photoArr={photosInterier} />
                    {/* <PhotosShower photoArr={photosInterier.map((url) => "/images/photos/" + url)} /> */}
                </Grid>
                <Grid xs={12} sm={6} item order={{ xs: -1, sm: 0 }}>
                    <Typography variant="h3" sx={{ mb: "16px", color: theme.palette.pink.main }}>интерьер</Typography>
                    <Typography>есть 3 пространства:</Typography>
                    <Typography sx={{ mb: "16px", color: theme.palette.background.default, display: "inline" }} fontWeight={"bold"}>верхний этаж </Typography>
                    <Typography sx={{ display: "inline" }}>- общее пространство с приставкой и теннисным столом</Typography>
                    <br /><br />
                    <Typography sx={{ mb: "16px", color: theme.palette.pink.main, display: "inline" }} fontWeight={"bold"}>нижний этаж</Typography>
                    <Typography sx={{ display: "inline" }}> - общее пространство, но более спокойное и уединенное </Typography>
                    <br /><br />
                    <Typography sx={{ color: "purple", display: "inline" }} fontWeight={"bold"}>2 комнаты кинотеатра</Typography>
                    <Typography sx={{ display: "inline" }}> - на большую компанию с проектором (даже диско шаром)</Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography variant="h3" sx={{ mb: "16px", color: theme.palette.pink.main }}>караоке</Typography>
                    <Typography>
                        комната оборудована лучшей на сегодняшний день караоке-системой AST 250. В каталоге более 70 000 песен. <br /><br />только голос и музыка...
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <PhotoShower photoArr={photosKaraoke} />
                    {/* <PhotosShower photoArr={photosKaraoke} /> */}
                </Grid>
            </Grid>
        </ContentWrapper>
    )
}

export default Interier;